.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
    display: none; /* Inicialmente oculto */
  }
  
  .popup {
    background: #fff;
    width: 298px;
    height: 328px;
    padding: 20px;
    border-radius: 8px;
    position: relative;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .popup-close {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    color: #000;
    font-weight: bold;
  }
  
  .popup-content {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .popup-icon {
    font-size: 48px;
    color: #FAA500;
    margin-bottom: 20px;
  }
  
  h2 {
    margin: 10px 0;
  }
  
  p {
    margin: 10px 0 20px;
  }
  
  input[type="email"] {
    width: calc(100% - 20px);
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  button {
    background-color: #FAA500;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
  
    svg {
      margin-left: 5px;
    }
  
    &:hover {
      background-color: #e69500;
    }
  }
  
  /* Media Queries */
  @media (max-width: 768px) {
    .popup-overlay {
      display: flex; /* Mostrar o pop-up em dispositivos m√≥veis */
    }
  }
  
  @media (min-width: 769px) {
    .popup-overlay {
      display: none; /* Ocultar o pop-up em dispositivos maiores */
    }
  }
  